
.map-card {
  width: 60%;
  min-width: 225px;
  max-width: 600px;
}
.info-content {
  max-width: 220px;
}
.active-element {
  border-left: 2px solid #0174e1;
  background-color: #eee;
}
.bottom-title {
  position: fixed;
  left: 96px;
  bottom: 26px;
  margin-right: 48px;
  padding: 16px;
  background-color: white;
  -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.07), 0 3px 1px -2px rgba(0, 0, 0, 0.06);
          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1), 0 2px 2px rgba(0, 0, 0, 0.07), 0 3px 1px -2px rgba(0, 0, 0, 0.06);
}
.fab-second-bottom {
  bottom: 100px !important;
}
.dialog-large .md-dialog {
  width: 80%;
}
@media (min-width: 600px) {
.bottom-left-sidenav {
    position: absolute;
    bottom: 1em;
    left: 1em;
}
.bottom-left-sidenav img {
    width: 50px;
}
}

/* overwrite default */
.md-dialog {
  width: 60%;
  min-width: 310px;
}
.md-list-item .md-radio {
  display: block;
}
.md-list-item .md-radio-container {
  float: left;
}
.md-sidenav .md-sidenav-content {
  max-width: 80%;
}
.md-dialog p {
  margin: 1em 0;
}



